

.modal {
	z-index: 10000;
	position: fixed;
	top: 0;
	width: 100vw;
	height: 100vh;
	background: rgba(0, 0, 0, 0.5);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 16px;
	h4 {
		margin-bottom: 16px;
	}
	> div {
		width: 500px;
		padding: 16px;
		padding-bottom: 0;
		background: white;
		box-shadow: 0 0 16px rgba(0, 0, 0, 0.5);
		> div {
			> p {
				margin: 0;
			}
		}
		> .dialog {
			margin-bottom: 16px;
			> p {
				margin-bottom: 16px;
			}
		}
		> .log {
			height: 200px;
			word-break: break-all;
			overflow-y: scroll;
			border: 1px solid #ddd;
			padding: 4px;
			margin-bottom: 16px;
		}
	}
}